document.addEventListener('DOMContentLoaded', () => {
  console.log('Footer script initialized');

  // –ü–ª–∞–≤–Ω–∞—è –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ –ø–æ –∫–ª–∏–∫—É –Ω–∞ —è–∫–æ—Ä–Ω—ã–µ —Å—Å—ã–ª–∫–∏ –≤ —Ñ—É—Ç–µ—Ä–µ
  document.querySelectorAll('.nav-footer a').forEach(link => {
    link.addEventListener('click', function (e) {
      e.preventDefault();
      const targetId = this.getAttribute('href');
      const targetElement = document.querySelector(targetId);
      if (targetElement) {
        targetElement.scrollIntoView({
          behavior: 'smooth'
        });
      } else {
        console.warn(`–≠–ª–µ–º–µ–Ω—Ç —Å id="${targetId}" –Ω–µ –Ω–∞–π–¥–µ–Ω –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ.`);
      }
    });
  });


  const copyright = document.querySelector('.copyright');
  if (copyright) {
    const currentYear = new Date().getFullYear();
    copyright.innerHTML = `&copy; ${currentYear} Relume. –í—Å—ñ –ø—Ä–∞–≤–∞ –∑–∞—Ö–∏—â–µ–Ω—ñ`;
  }
});
